import React from "react";
import PreLoader from "../../common/preLoader/preloader";

const ProfileInfo = (props) => {
  if (!props.profile) {
    return <PreLoader />;
  }

  return (
    <div>
      <div style={{ padding: "3rem" }}>
        <img src={props.profile.photos.large} alt="ava" />
      </div>
    </div>
  );
};

export default ProfileInfo;
